.projects_container{
    max-width: 70vw;
    width: 100%;
    height: 180vh;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    padding-top: 30px;
}
.projects_header{
    width: 100%;
    height: 100px;
    position: relative;
    margin-bottom: 30px;
}
.projects_body{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.projects_box{
    width: 100%;
    height: 85%;
    display: flex; 
    flex-direction: row;
    align-items: center;
    padding-bottom: 10px;
    margin-left: 5vw;
}
.projects_image{
    /* width: 500px; */
    width: clamp(500px, 50%, 800px);
    /* width: 50%; */
    height: 90%;
    border-radius: 10px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    transition: 0.3s;
    z-index: 1;
    filter: grayscale(50%);
    cursor: pointer;
    /* margin-left: 20px; */
}
.projects_image:hover{
    transform: scale(1.03);
    filter: grayscale(0%);
}
.projects_description{
    width: 40%;
    height: 100%;
    z-index: 2;
    display: flex;
    flex-direction: column;
}
.projects_description h1{
    font-size: clamp(1.5rem, 2vw, 4rem);
}
.projects_description_r{
    /* margin-left: -90px; */
    padding-left: 25px;
    padding-right: 25px;
    align-items: left; 
    justify-content: center;
    text-align: left;
}
.projects_description_l{
    /* margin-right: -90px; */
    padding-left: 25px;
    padding-right: 25px;
    /* align-items: left; */
    justify-content: center;
    text-align: right;
}
.projects_blurb{
    width: 100%;
    height: 120px;
    border-radius: 10px;
    /* background-color: var(--darkerWhite); */
    /* background-color: #434343; */
    /* padding-left: 20px; */
    display: flex;
    flex-direction: column;
    align-items: right;
    justify-content: center;
}
.projects_blurb p{
    font-size: calc(0.1rem + 1vw); 
    color: #5e5e5e;
}
.projects_tech{
    margin-top: 5px;
}
.projects_tech li{ 
    background-color: grey;
    display: inline-block;
    border-radius: 15px;
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    pointer-events: none;
}
.projects_tech > *{
    padding: 3px 0.75rem 3px 0.85rem;
    font-size: 11px;
    margin-right: 10px;
}
.projects_link{
    width: 100%;  
    height: 20px;
    display: flex;
    flex-direction: row;
}
.projects_link_r{
    justify-content: flex-start;
    margin-left: -10px;
}
.projects_link_l{
    /* justify-content: flex-start; */
    justify-content: flex-end;
    margin-right: -10px;
}
.projects_link >*{
    /* padding-left: 0.75rem; */
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    transition: 0.3s;
}
.projects_link >*:hover{
    color: var(--secondaryColor);
}


/* @media only screen and (max-width: 768px){ */
@media only screen and (max-width: 810px){
    .projects_container{
        max-width: 75vw;
        max-height: 160vh;
    }
    .projects_box{
        /* flex-direction: column; */
        position: relative;
        
    }
    .projects_box:after {
        z-index: 1;
        content: '\A';
        position: absolute;
        width: 100%; height: 95%;
        top:0; left:0;
        background:rgba(0,0,0,0.5);
        opacity: 1;
        transition: all 1s;
        -webkit-transition: all 1s;
        border-radius: 10px;
    }
    .projects_image{
        position: relative;
        top: 0;
        /* margin-left: 0px; */
        width: 100%;
        height: 100%;
        filter: grayscale(80%);
        filter: blur(1px);
    }
    .projects_decsription{
        width: 100%;
        align-items: left;
    }
    .projects_description h1{
        font-size: 25px;
    }
    .projects_description_r{
        position: relative;
        margin-left: -99%;
        top: 0;
        height: 100%;
        width: 100%;
    }
    .projects_description_l{
        position: relative;
        margin-right: -100%;
        top: 0;
        height: 100%;
        width: 100%;    
    }
    .projects_description{
        color: white;
        text-align: left;
    }
    .projects_blurb p{
        color: white;
        text-align: left;
        font-size: 14px;
    }
    .projects_link{
        justify-content: flex-start;
    }
}


